@font-face {
    font-family: "font-1";
    src: url(../assets/fonts/OpenSans-VariableFont_wdth,wght.ttf);
}

$font-1: "font-1", sans-serif;

$color-1: #61dafb;
$color-2: salmon;
$color-3: #282c34;
$white: rgb(243, 243, 243);

@mixin verticalCenter {
    display: flex;
    justify-content: center;
    align-items: center;
}

* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}

body {
    background: $white;
    font-family: $font-1;
    padding: 16px;
    // @include verticalCenter;
}

li {
    list-style-type: none;
}

input[type="text"] {
    padding: 8px 12px;
    width: 100%;
    max-width: 300px;
    border: 1px solid #e0e0e0;
    border-radius: 20px;
}