@keyframes anvil {
    0% {
        transform: scale(1) translateY(0px);
        opacity: 0;
        box-shadow: 0 0 0 rgba(241, 241, 241, 0);
    }

    1% {
        transform: scale(0.96) translateY(10px);
        opacity: 0;
        box-shadow: 0 0 0 rgba(241, 241, 241, 0);
    }

    100% {
        transform: scale(1) translateY(0px);
        opacity: 1;
        box-shadow: 0 0 500px rgba(241, 241, 241, 0);
    }
}

.popup-content {
    margin: auto;
    background: rgb(255, 255, 255);
    width: 50%;
    padding: 5px;
    border: none;
    border-radius: 20px;
    -webkit-animation: anvil 0.3s cubic-bezier(0.38, 0.1, 0.36, 0.9) forwards;
}

.popup-arrow {
    color: rgb(255, 255, 255);
}

[role='dialog'].popup-content {
    width: 600px;
    height: auto;
    padding: 16px;
    position: relative;

    .title {
        font-size: 1rem;
        font-weight: bold;
        margin-bottom: 16px;
        text-align: center;
    }

    p {
        max-height: 350px;
        line-height: 22px;
        overflow: scroll;
        --mask: linear-gradient(to bottom,
                rgba(0, 0, 0, 1) 0, rgba(0, 0, 0, 1) 85%,
                rgba(0, 0, 0, 0) 105%, rgba(0, 0, 0, 0) 0) 100% 50% / 120% 100% repeat-x;
        -webkit-mask: var(--mask);
        mask: var(--mask);
        padding-bottom: 32px;
    }

    .close {
        display: block;
        position: absolute;
        top: 0;
        right: 12px;
        font-weight: bold;
        font-size: 2rem;
        cursor: pointer;

        &:hover {
            color: #28a1ed;
        }
    }
}

.popup-overlay {
    background: rgba(0, 0, 0, 0.5);
}

[data-popup=tooltip].popup-overlay {
    background: #0d0d0d99;
}