.search-container {
    text-align: center;
    margin-bottom: 20px;
}

.meals {

    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    gap: 16px;

    li {
        width: 300px;
        height: auto;
        background-color: #e8e8e8;
        overflow: hidden;
        padding: 16px;

        h2 {
            font-size: 1rem;
            text-align: center;
            margin-bottom: 10px;
        }

        h3 {
            font-size: 0.8rem;
            text-align: center;
            font-weight: normal;
            margin-bottom: 16px;
        }

        img {
            width: 100%;
            height: auto;
        }

        p {
            font-size: 0.8rem;
            line-height: 22px;
            display: -webkit-box;
            -webkit-box-orient: vertical;
            -webkit-line-clamp: 6;
            overflow: hidden;
            text-overflow: ellipsis;
            margin-top: 16px;
        }

        .button-container {
            text-align: center;
            margin: 16px auto 0;

            button {
                background-color: #031d2d;
                padding: 8px 16px;
                border-radius: 20px;
                border: none;
                color: #fff;
                cursor: pointer;
                transition-duration: 0.5s;

                &:hover {
                    background-color: #073a5a;
                    letter-spacing: 1px;
                }
            }
        }
    }
}